import React from 'react'
import "./Navbar.css"
import menuItems from "../../menuItems"
import MenuItems from "./MenuItems"
import { Link, useHistory } from 'react-router-dom'
import {memo} from "react";




const Navbar = () => {
    const auth=localStorage.getItem('token');
    const history=useHistory();
    const logout=()=>{
        localStorage.clear();
        history.push('/signup');
    }

    return (
        <>
            <nav className="main-nav">
                <div className="logo">
                    <Link to="/" className="logo-a">LOGO</Link>
                </div>
                <div className="menu-links">
                    <ul className="menus">
                        {
                            menuItems.map((menu,index)=>{
                                const depthLevel=0;
                                return <MenuItems items={menu} key={index} depthLevel={depthLevel}/>;
                            })
                        }
                    </ul>
                </div>

                <div className="buttons">
                    {
                        auth? 
                        <ul className='=withauth'>
                        <Link onClick={logout} to="/signup" className="dropbtn-c">Logout</Link>
                        </ul>
                        :
                        <ul className="withoutauth">
                        <Link to="/login" className="dropbtn-c">Login</Link>
                        <Link to="/signup" className="dropbtn-c">SignUp</Link>
                        </ul>
                        
                        
                    }
                </div>

            </nav>
        </>
    )
}

export default memo(Navbar)