import {Route} from "react-router-dom";

import Home from "../components/Quiz/Home";
import QuizState from "../context/QuizState";

import Alert from "../components/Quiz/Alert";
import Login from "./Login";
import PlayQuizEntry from "../components/Quiz/PlayQuizEntry";
import Signup from "./Signup";
import{ useState } from 'react';

const Quiz=()=> {
  const[alert, setAlert] = useState(null);
  const showAlert =(message,type)=> { //to show alert messages
    setAlert({
      msg: message,
      type: type
    })
    setTimeout(() =>{
      setAlert(null)
    },1500)
  }
  return (
    <>
      <QuizState>
          <Alert alert={alert}/>
          <div className="container">
         
          <Route path="/quiz" render={(props) => <Home {...props} showAlert={showAlert}/>}/>
              <Route  path="/quiz/playquiz" component={PlayQuizEntry}/>
             
           
          </div>
       
      </QuizState>
    </>
  );
}

export default Quiz;